<template>
  <div>
      <nav class="navbar navbar-expand-md navbar-dark fixed-top bg-dark">
        <a class="navbar-brand" href="/">誰來找茶</a>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarCollapse"
          aria-controls="navbarCollapse"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarCollapse">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item active mr-3">
              <router-link class="nav-link" to="/">首頁</router-link>
            </li>
            <li class="nav-item mr-3">
              <router-link class="nav-link" to="/customer_order">產品列表</router-link>
            </li>
            <li class="nav-item mr-3">
              <router-link class="nav-link" to="/about">關於我們</router-link>
            </li>
            <li class="nav-item mr-3">
              <router-link class="nav-link" to="/contact">聯繫我們</router-link>
            </li>
            <li class="nav-item mr-3">
              <span class="cartQuantity text-white bg-danger">{{cart.carts.length}}</span>
              <a href="#" data-toggle="modal" data-target="#cartModal"><i class="fas fa-shopping-cart fa-2x text-success"></i></a>
            </li>
          </ul>
        </div>
      </nav>
      <!-- 購物車Modal -->
    <div
      class="modal fade"
      id="cartModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title font-weight-bold ml-auto" id="exampleModalLabel">購物車清單</h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="my-5 row justify-content-center" v-if="cart.final_total > 0">
      <div class="col-md-6">
        <table class="table" >
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <!-- <div class="text-success" v-if="item.coupon">
          已套用優惠券
        </div> -->
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}元</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}元</td>
            </tr>
            <!-- 隱藏此格，有折扣才會出現 -->
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}元</td>
            </tr>
          </tfoot>
        </table>
      </div>
            </div>
            <div class="h5 text-success text-center" v-else>
              <p>購物車內沒有東西喔，快去選購吧!</p>
              <router-link class="btn btn-success" to="/customer_order">繼續選購</router-link>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              關閉
            </button>
            <router-link class="btn btn-danger" to="/form_check">前往結帳</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import OrderBus from "@/OrderBus.js";
import $ from "jquery";
import HeaderBus from "@/HeaderBus.js";
import FormBus from "@/FormBus.js";

export default {
  components:{
    OrderBus,
    HeaderBus,
    FormBus,
  },
  data() {
    return {
      cart: {
        carts:[],
      },
    };
  },
   methods: {
    getProducts(page = 1) {
      //取得遠端產品資料
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      const vm = this;
      //API伺服器路徑及自訂API路徑,這兩項因不同API會改變,因此特別抓出來做變數
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        //看遠端撈的資料變數與所需的是否一致
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        vm.product.num = ""; //使產品數量為空,避免select選單遭綁定
        console.log(response);
        vm.status.loadingItem = "";
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        vm.status.loadingItem = "";
        vm.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cart = response.data.data;
        vm.dataHeaderBus();
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      // vm.isLoading = true;
      this.$http.post(url, { data: order }).then((response) => {
        if (response.data.success) {
          vm.$router.push(`/customer_checkout/${response.data.orderId}`);
        } else {
          console.log("購物車沒東西");
        }
        vm.isLoading = false;
      });
    },
    dataHeaderBus(){
      HeaderBus.$emit("header-data", this.cart);
    },
    getFromOrderBus(){
      OrderBus.$on("order-data",(orderdata) => (this.cart = orderdata));
    },
    getFromFormBus(){
      FormBus.$on("form-data",(formdata) => (this.cart = formdata));
    },
  },
  created() {
    this.getProducts();
    this.getCart();
    this.getFromOrderBus();
    this.getFromFormBus();
  },
}
</script>

<style scoped>
.cartQuantity{
  position:absolute;
  width: 1.5rem;
  height: 1.5rem;
  border-radius:50%;
  font-size: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translate(1rem,-0.5rem);
  
}  
.nav-item{
  font-size: 1rem;
}
</style>


