<template>
  <div class="container-fluid mt-5 pt-1">
    <h3 class="text-center mt-3">訂單資料</h3>
    <!-- 購物車內容 -->
    <div class="my-5 row justify-content-center" v-if="cart.final_total > 0">
      <div class="col-md-6">
        <table class="table">
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="removeCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <!-- <div class="text-success" v-if="item.coupon">
          已套用優惠券
        </div> -->
              </td>
              <td class="align-middle">
                {{ item.qty }}/{{ item.product.unit }}
              </td>
              <td class="align-middle text-right">{{ item.final_total }}元</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total }}元</td>
            </tr>
            <!-- 隱藏此格，有折扣才會出現 -->
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total }}元</td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
    <div class="text-center mt-3" v-if="cart.final_total <= 0">
      <p class="h5 text-success">購物車內沒有東西喔，快去選購吧!</p>
      <router-link class="btn btn-success" to="/customer_order">繼續選購</router-link>
    </div>
    <div  class="text-center mt-3" v-else>
      <router-link class="btn btn-danger" to="/customer_order">回去選購</router-link>
    </div>
    <!-- 表單內容 -->
    <div class="my-5 row justify-content-center">
      <div class="col-md-6">
        <validation-observer v-slot="{ invalid }">
          <form @submit.prevent="createOrder">
            <validation-provider
              rules="required|email"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <!-- 輸入框 -->
              <label for="useremail">Email</label>
              <input
                id="useremail"
                type="email"
                name="email"
                v-model="form.user.email"
                class="form-control"
                :class="classes"
                placeholder="請輸入 Email"
              />
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback">Email 正確</span>
            </validation-provider>
            <validation-provider
              rules="required"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <!-- 輸入框 -->
              <label for="username">收件人姓名</label>
              <input
                id="username"
                type="text"
                name="姓名"
                v-model="form.user.name"
                class="form-control"
                :class="classes"
                placeholder="請輸入姓名"
              />
              <!-- 錯誤訊息 -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback"></span>
            </validation-provider>
            <validation-provider
              rules="required"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <label for="usertel">收件人電話</label>
              <input
                id="usertel"
                type="tel"
                name="電話"
                v-model="form.user.tel"
                class="form-control"
                :class="classes"
                placeholder="請輸入電話"
              />
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback"></span>
            </validation-provider>
            <validation-provider
              rules="required"
              class="form-group"
              tag="div"
              v-slot="{ errors, classes, passed }"
            >
              <label for="useraddress">收件人地址</label>
              <input
                id="useraddress"
                type="address"
                name="地址"
                v-model="form.user.address"
                class="form-control"
                :class="classes"
                placeholder="請輸入地址"
              />
              <span class="invalid-feedback">{{ errors[0] }}</span>
              <span v-if="passed" class="valid-feedback"></span>
            </validation-provider>
            <div class="form-group">
              <label for="useraddress">留言</label>
              <textarea
                name=""
                id=""
                class="form-control"
                cols="30"
                rows="10"
                v-model="form.message"
              ></textarea>
            </div>
            <div class="text-right">
              <button type="submit" class="btn btn-primary" :disabled="invalid">
                送出訂單
              </button>
            </div>
          </form>
        </validation-observer>
      </div>
    </div>
  </div>
</template>
<script>
import OrderBus from "@/OrderBus.js";
import HeaderBus from "@/HeaderBus.js";
import FormBus from "@/FormBus.js";
import $ from "jquery";

export default {
  components: {
    OrderBus,
    HeaderBus,
    FormBus,
  },
  name:'FormCheck',
  data() {
    return {
      cart: {
        carts: [],
      },
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
    };
  },
  methods: {
    getProducts(page = 1) {
      //取得遠端產品資料
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      const vm = this;
      //API伺服器路徑及自訂API路徑,這兩項因不同API會改變,因此特別抓出來做變數
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        //看遠端撈的資料變數與所需的是否一致
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        vm.product.num = ""; //使產品數量為空,避免select選單遭綁定
        console.log(response);
        vm.status.loadingItem = "";
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        vm.status.loadingItem = "";
        vm.getCart();
        $("#productModal").modal("hide");
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cart = response.data.data;
        vm.dataFormBus();
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      // vm.isLoading = true;
      this.$http.post(url, { data: order }).then((response) => {
        if (response.data.success) {
          vm.$router.push(`/customer_checkout/${response.data.orderId}`);
        } else {
          console.log("購物車沒東西");
        }
        vm.isLoading = false;
      });
    },
    getFromOrderBus(){
      OrderBus.$on("order-data",(orderdata) => (this.cart = orderdata));
    },
    getFromHeaderBus(){
      HeaderBus.$on("header-data",(headerdata) => (this.cart = headerdata));
    },
    dataFormBus(){
      FormBus.$emit("form-data", this.cart);
    },
  },
  created() {
    this.getProducts();
    this.getCart();
    this.getFromOrderBus();
    this.getFromHeaderBus();
  },
};
</script>


