<template>
  <div>
    <Header></Header>
    <router-view></router-view>
    <Footer></Footer>
    <!-- 回到最上方按鈕 -->
    <div>
      <button
        v-if="scrollvalue"
        class="btn btn-danger backtotop"
        @click="scrolltotop"
      >
        <i class="fas fa-arrow-up"></i>
      </button>
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import Footer from "./Footer";
import HomeContent from "./HomeContent";
import CustomerOrder from "./CustomerOrders";
import About from "./About";
import Contact from "./Contact";

export default {
  components: {
    Header,
    Footer,
    HomeContent,
    CustomerOrder,
    About,
    Contact,
  },
  data() {
    return {
      scrollvalue: false,
    };
  },
  methods: {
    //滾動事件語法，最到最上方
    scrolltotop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    btnscroll() {
      var scrollTop =
        document.body.scrollTop || document.documentElement.scrollTop;
      if (scrollTop > 500) {
        this.scrollvalue = true;
      } else {
        this.scrollvalue = false;
      }
    }, 
  },
  created() {
  },
  //監聽滾動事件
  mounted() {
    window.addEventListener("scroll", this.btnscroll);
  },
};
</script>

<style scoped>
.btn {
  border-width: 2px;
}
.bg-banner1 {
  background-image: url(../../assets/img/tea1.jpg);
}
.bg-banner2 {
  background-image: url(../../assets/img/tea2.jpg);
}
.bg-banner3 {
  background-image: url(../../assets/img/tea3.jpg);
}
.header-carousel-item {
  height: 40rem;
}

.bg-cover {
  background-size: cover;
  background-position: center center;
}
.bg-imgleft {
  position: absolute;
  top: 0;
  bottom: 0;
  background-image: url(../../assets/img/tea4.jpg);
}
.bg-imgright {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  background-image: url(../../assets/img/tea5.jpg);
}
.bg-text1 {
  color: #a9a9a9;
}
.carousel-caption {
  background-color: rgba(0, 0, 0, 0.4);
}

.font-title {
  font-size: 28px;
}

.border-none {
  border: none !important;
}
.backtotop {
  position: fixed;
  bottom: 0;
  right: 0;
  border-radius: 50%;
  margin-right: 5px;
  margin-bottom: 5px;
}
@media (min-width: 768px) {
  .text-md-dark {
    color: #333;
  }
}
</style>


